import { useSelector, useDispatch } from "react-redux";
import { Panel, Button } from "../styled";
import {
    selectTasksNotEmpty,
    selectAllTasksDone,
    selectHideDone,
    toggleHideDone,
    setAllDone,
} from "../../../tasksSlice";

const TaskButtonsControlPanel = () => {
    const hideDone = useSelector(selectHideDone);
    const isAllTasksDone = useSelector(selectAllTasksDone);
    const tasksNotEmpty = useSelector(selectTasksNotEmpty);
    const dispatch = useDispatch();

    return (
        <Panel>
            {tasksNotEmpty && (
                <>
                    <Button onClick={() => dispatch(toggleHideDone())}>
                        {hideDone ? "Poka ukoczone" : "Ukryj ukoczone"}
                    </Button>
                    <Button
                        onClick={() => dispatch(setAllDone())}
                        disabled={isAllTasksDone}
                    >
                        Ukocz wszystkie
                    </Button>
                </>
            )}
        </Panel>
    );
};

export default TaskButtonsControlPanel;