import React from 'react';
import { useDispatch, useSelector } from "react-redux";
import { Button } from "../styled";
import { fetchExampleTasks, selectIsError, selectIsLoading, setError } from "../../../tasksSlice";

const ShowExampleTasksButton = () => {
    const isLoading = useSelector(selectIsLoading);
    const isError = useSelector(selectIsError);
    const dispatch = useDispatch();

    const handleClick = () => {
        dispatch(setError(false));
        dispatch(fetchExampleTasks());
    };

    return (
        <>
            <Button onClick={handleClick} disabled={isLoading}>
                {isLoading ? "Loading..." : "Pobierz przykadowe zadania"}
            </Button>
            {isError &&
                <div style={{ color: 'red' }}>
                    Nie udao si zaadowa zada. Sprbuj ponownie pniej.
                </div>}
        </>
    );
};

export default ShowExampleTasksButton;
